  transition: all 0.2s ease-out;
  -moz-transition: all 0.2s ease-out;
  -webkit-transition: all 0.2s ease-out;
  -o-transition: all 0.2s ease-out;
}
.col.project:hover {
  cursor: pointer;
  background-color: #fff;
}
.project-gfx {
  background-size: cover;
  background-color: #000;
  height: 200px;
  padding: 0;
}
#work {
  margin: 0px auto 0px auto;
}
.col-lg-1,
.col-lg-2,
.col-lg-3,
.col-lg-4,
.col-lg-5,
.col-lg-6,
.col-lg-7,
.col-lg-8,
.col-lg-9,
.col-lg-10,
.col-lg-11,
.col-lg-12 {
  float: none;
}
.col-float.col-lg-1,
.col-float.col-lg-2,
.col-float.col-lg-3,
.col-float.col-lg-4,
.col-float.col-lg-5,
.col-float.col-lg-6,
.col-float.col-lg-7,
.col-float.col-lg-8,
.col-float.col-lg-9,
.col-float.col-lg-10,
.col-float.col-lg-11,
.col-float.col-lg-12 {
  float: left;
}
a,
button {
  color: #000000;
  text-decoration: underline;
  transition: color 0.2s ease-out;
  -moz-transition: color 0.2s ease-out;
  -webkit-transition: color 0.2s ease-out;
  -o-transition: color 0.2s ease-out;
}
p:hover a {
  color: #ff00cc;
}
a:hover,
a:active,
a:focus {
  color: #ff00cc;
  outline: none;
}
.meta a {
  color: #5b5b5b;
}
.meta a:hover {
  color: #ff00cc;
}
.project a.more-info {
  opacity: 0;
  position: absolute;
  bottom: 12px;
  width: 100%;
  display: block;
  margin: 0 auto;
}
.col.project:hover a.more-info {
  color: #ff00cc;
  opacity: 1;
}
ul {
  margin: 0;
  padding: 0;
  list-style: none;
}
.content .networks {
  font-size: 1.4em;
  margin-top: 60px;
  margin-bottom: 10px;
}
.content .networks .col-float {
  margin-bottom: 40px;
}
.content ul.networks li {
  margin-bottom: 30px;
}
ul.networks li {
  float: left;
  margin-bottom: 10px;
}
ul.networks a,
.content .networks a {
  color: #808080;
  text-decoration: none;
}
ul.networks a {
  margin-right: 20px;
}
ul.networks li:last-child a {
  margin-right: 0;
}
.networks a:hover {
  color: #ff00cc;
}
ul.tags {
  font-size: 0.65em;
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
  background-color: #fff;
  transition: all 0.2s ease-out;
  -moz-transition: all 0.2s ease-out;
  -webkit-transition: all 0.2s ease-out;
  -o-transition: all 0.2s ease-out;
}
.col.project ul.tags {
  font-size: 1em;
  padding: 10px 10px;
  opacity: 0;
}
.col.project:hover ul.tags {
  opacity: 0.9;
}
ul.tags li {
  float: left;
  display: inline-block;
  margin: 0px 10px 0px 0px;
}
.project ul.tags li {
  float: right;
  margin: 0px 0px 7px 7px;
}
.project:nth-child(even) ul.tags li {
  float: left;
  margin: 0px 7px 7px 0px;
}
ul.tags div {
  border: 1px solid #5d5d5d;
  background: transparent;
  color: #5d5d5d;
  text-decoration: none;
  padding: 5px 10px;
  border-radius: 3px;
  background-clip: padding-box;
  line-height: 1;
  display: inline-block;
  -webkit-font-smoothing: subpixel-antialiased;
  -moz-osx-font-smoothing: auto;
}
p {
  font-size: 0.75em;
  line-height: 1.4em;
  text-align: left;
  padding: 0;
  margin: 30px 0px;
  color: #000000;
}
p span {
