}
/*
* Nav overlay
*/
#nav-container {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
}
.nav-btn {
  position: relative;
  width: 32px;
  height: 46px;
  -webkit-transition-duration: 0.2s;
  transition-duration: 0.2s;
  margin: 0 auto 0 auto;
  z-index: 999;
  /* 		background: #ff00bb; */
}
.nav-btn.open {
  height: 86px;
}
.nav-btn .icon {
  -webkit-transition-duration: 0.2s;
  transition-duration: 0.2s;
  position: absolute;
  height: 40px;
  width: 32px;
  top: 0px;
  left: 0px;
}
.nav-btn .icon:before {
  -webkit-transition-duration: 0.2s;
  transition-duration: 0.2s;
  position: absolute;
  width: 32px;
  height: 3px;
  background-color: #d8d8d8;
  content: "";
  top: 33px;
  left: 0;
}
.nav-btn.open .icon:before {
  background-color: #ababab;
}
.nav-btn .icon:after {
  -webkit-transition-duration: 0.2s;
  transition-duration: 0.2s;
  position: absolute;
  width: 32px;
  height: 3px;
  background-color: #d8d8d8;
  content: "";
  top: 43px;
  left: 0;
}
.nav-btn.open .icon:after {
  background-color: #ababab;
}
.nav-btn.open .icon {
  -webkit-transition: 0.5s;
  transition: 0.5s;
  z-index: 999;
  top: 30px;
}
.nav-btn.open .icon:before {
  -webkit-transform: rotateZ(45deg) scaleX(1.25) translate(3px, 3px);
  -ms-transform: rotateZ(45deg) scaleX(1.25) translate(3px, 3px);
  transform: rotateZ(45deg) scaleX(1.25) translate(3px, 3px);
}
.nav-btn.open .icon:after {
  -webkit-transform: rotateZ(-45deg) scaleX(1.25) translate(3px, -3px);
  -ms-transform: rotateZ(-45deg) scaleX(1.25) translate(3px, -3px);
  transform: rotateZ(-45deg) scaleX(1.25) translate(3px, -3px);
}
.nav-btn:hover {
  cursor: pointer;
}
.nav-btn:hover .icon:before,
.nav-btn:hover .icon:after {
  background-color: #000;
}
.nav-btn.open:hover .icon:before,
.nav-btn.open:hover .icon:after {
  background-color: #fff;
}
.menu {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.95);
  color: #fff;
  z-index: 998;
  opacity: 0;
  visibility: hidden;
  -webkit-transition: opacity 0.3s, visibility 0s 0.3s;
  transition: opacity 0.3s, visibility 0s 0.3s;
}
.menu.open {
  opacity: 1;
  visibility: visible;
  -webkit-transition: opacity 0.3s;
  transition: opacity 0.3s;
}
.menu nav {
  text-align: center;
  position: relative;
  top: 50%;
  -webkit-transform: translateY(-50%);
  -ms-transform: translateY(-50%);
  transform: translateY(-50%);
}
.menu nav ul li a {
  margin: 0.7em 0;
  font-size: 2.5em;
  display: inline-block;
  color: #fff;
  text-decoration: none;
}
.menu nav ul li a:hover {
  text-decoration: underline;
}
