html, body {
	font-family: Arial,sans-serif;
	text-align: center;
	font-size: 24px;
	background: #fff;
    transition: background-color .2s ease-out;
    -moz-transition: background-color .2s ease-out;
    -webkit-transition: background-color .2s ease-out;
    -o-transition: background-color .2s ease-out;	
}

body.fixed {
	overflow: hidden;
}

footer {
	border-top: 1px solid #d8d8d8;	
	padding: 30px 0 50px 0;
	margin-top: 30px;
}

.col {
	margin: 0 auto;
}

.col.project {
	position: relative;
	margin-bottom: 40px;
    transition: all .2s ease-out;
    -moz-transition: all .2s ease-out;
    -webkit-transition: all .2s ease-out;
    -o-transition: all .2s ease-out;		
}
.col.project:hover {
	cursor: pointer;
	background-color: #fff;
}
.project-gfx {
	background-size: cover; 
	background-color: #000;
	height: 200px; 
	padding: 0;	
}

.col.project:hover .project-gfx {
}

#work {
	margin: 0px auto 0px auto;
}

.col-lg-1, 
.col-lg-2, 
.col-lg-3, 
.col-lg-4, 
.col-lg-5, 
.col-lg-6, 
.col-lg-7, 
.col-lg-8, 
.col-lg-9, 
.col-lg-10, 
.col-lg-11, 
.col-lg-12 {
    float: none;
}

.col-float.col-lg-1, 
.col-float.col-lg-2, 
.col-float.col-lg-3, 
.col-float.col-lg-4, 
.col-float.col-lg-5, 
.col-float.col-lg-6, 
.col-float.col-lg-7, 
.col-float.col-lg-8, 
.col-float.col-lg-9, 
.col-float.col-lg-10, 
.col-float.col-lg-11, 
.col-float.col-lg-12 {
    float: left;
}	

a, button {
	color: @text-color;
	text-decoration: underline;
    transition: color .2s ease-out;
    -moz-transition: color .2s ease-out;
    -webkit-transition: color .2s ease-out;
    -o-transition: color .2s ease-out;	
}

p:hover a {
	color: @highlight-color;	
}

a:hover, a:active, a:focus {
	color: @highlight-color;
    outline: none;	
}

.meta a {
	color: #5b5b5b;
}

.meta a:hover {
	color: @highlight-color;	
}

.project a.more-info {
	opacity: 0;
	position: absolute;
	bottom: 12px;
	width: 100%;
	display: block;
	margin: 0 auto;
}

.col.project:hover a.more-info {
	color: @highlight-color;	
	opacity: 1;
}

ul {
	margin: 0; padding: 0;
	list-style: none;
	
}

.content .networks {
	font-size: 1.4em;
	margin-top: 60px;
	margin-bottom: 10px;	
}
.content .networks .col-float {
	margin-bottom: 40px;	
}

.content ul.networks li {
	margin-bottom: 30px;	
}

ul.networks li {
	float: left;	
	margin-bottom: 10px;
}

ul.networks a,
.content .networks a {
	color: #808080;
	text-decoration: none;
}
ul.networks a {
	margin-right: 20px;	
}
ul.networks li:last-child a  {
	margin-right: 0;
}

.networks a:hover {
	color: @highlight-color;
}

ul.tags {
	font-size: 0.65em;
	margin: 0; padding: 0;
	width: 100%; height: 100%;
	background-color: #fff;
    transition: all .2s ease-out;
    -moz-transition: all .2s ease-out;
    -webkit-transition: all .2s ease-out;
    -o-transition: all .2s ease-out;	
}
.col.project ul.tags {
	font-size: 1em;	
	padding: 10px 10px;
	opacity: 0;
}
.col.project:hover ul.tags {
	opacity: 0.9;	
}

ul.tags li {
	float: left;
	display: inline-block;	
	margin: 0px 10px 0px 0px;	
}
.project ul.tags li {
	float: right;
	margin: 0px 0px 7px 7px;	
}
.project:nth-child(even) ul.tags li {
	float: left;	
	margin: 0px 7px 7px 0px;	
}

ul.tags div {
	border: 1px solid #5d5d5d;
	background: transparent;
	color: #5d5d5d;
	text-decoration: none;
	padding: 5px 10px;
    border-radius: 3px;
	background-clip: padding-box;
    line-height: 1;
	display: inline-block;
	-webkit-font-smoothing: subpixel-antialiased;
	-moz-osx-font-smoothing: auto;	
}

p {
	font-size: 0.75em;
	line-height: 1.4em;
	text-align: left;
	padding: 0; margin: 30px 0px;
	color: @text-color;
}

p span {
	color: @highlight-color;
}

.project {
	font-size: 0.6em;
}
.project:nth-child(even) p,
.project:nth-child(even) h3 {
    text-align: right;
}
.project p {
	margin: 0px 0px 3px 0px;	
}
.project p.project-url {
	margin: 0px 0px 25px 0px;	
}

p.meta b {
	color: #333;
}

h1, h2, h3 {
	font-family: Arial,sans-serif; 	
	text-align: left;	
	padding: 0; margin: 0;		
	color: #000;		
	-webkit-transition: all 0.1s ease;
	-moz-transition: all 0.1s ease;
	-o-transition: all 0.1s ease;
	-ms-transition: all 0.1s ease;			
}

h3 {
	font-size: 28px;
	line-height: 34px;		
	font-weight: 400;
	letter-spacing: -0.01em;		
	margin-top: 5px;	
}

h2 {
	font-size: 36px;
	line-height: 42px;		
	font-weight: 400;
	letter-spacing: -0.01em;		
	margin: 40px 0px 10px 0px;	
}

h1 {
	text-align: center;
	-webkit-transition: all 0.1s ease;
	-moz-transition: all 0.1s ease;
	-o-transition: all 0.1s ease;
	-ms-transition: all 0.1s ease;	
}

h1 span {
	color: @highlight-color;
}

	header h1 {
		font-size: 26px;
		line-height: 34px;		
		font-weight: 600;
		letter-spacing: -0.03em;		
		color: #000;	
		margin: 30px 0px 10px 0px;
	}

	@media screen and (min-width: 480px) {
		header h1 {
			font-size: 36px;
			line-height: 44px;	
		}
		p {
			font-size: 0.83em;
			line-height: 1.5em;		
			margin: 40px 0px;
		}		
	}

	@media screen and (min-width: 768px) {
		header h1 {
			font-size: 43px;
			line-height: 52px;	
			margin-top: 50px;			
		}		
		p {
			font-size: 0.83em;
			line-height: 1.46em;		
		}			
	}
	
	@media screen and (min-width: 992px) {
		header h1 {
			font-size: 50px;
			line-height: 58px;	
			margin: 70px 0px 20px 0px;
		}		
		p {
			font-size: 1em;
			line-height: 1.4em;		
		}
		ul.networks a {
			margin-right: 40px;	
		}
	}
	
	@media screen and (min-width: 1200px) {
		header h1 {
			font-size: 56px;
			line-height: 64px;
		}
		ul.networks a {
			margin-right: 40px;	
		}				
	}		

	section h1 {
		color: #000;
		font-size: 56px;
		line-height: 64px;	
		letter-spacing: -0.03em;
		font-weight: 600;	
		padding: 0;  margin-bottom: 50px; 
		border: 0; border-radius: 0;	
	}
	
	section h1 a,
	section h1 span {
		color: @highlight-color;
		text-decoration: underline;	
	}

.btn {
	color: #ff00cc;
	background: #fff;	
	padding: 12px;
    border-radius: 5px;
	background-clip: padding-box;
    line-height: 1;
	display: inline-block;
	cursor: pointer;
	text-decoration: none;
	vertical-align: top;
	-webkit-font-smoothing: subpixel-antialiased;
	-moz-osx-font-smoothing: auto;
	border: 1px solid #ff00cc;
}

a.btn {
	text-decoration: none;
}

a.btn:hover {
	background: @highlight-color;
	color: #fff;
}

/*
* Nav overlay
*/

#nav-container {
	position: absolute;
	top: 0; left: 0;
	width: 100%;
}

.nav-btn {
	position: relative;
	width: 32px;
	height: 46px;
	-webkit-transition-duration: 0.2s;
	        transition-duration: 0.2s;
	margin: 0 auto 0 auto;
	z-index: 999;
/* 		background: #ff00bb; */
}
.nav-btn.open {
	height: 86px;		
}
.nav-btn .icon {
	-webkit-transition-duration: 0.2s;
	        transition-duration: 0.2s;
	position: absolute;
	height: 40px;
	width: 32px;
	top: 0px; left: 0px;
}
.nav-btn .icon:before {
	-webkit-transition-duration: 0.2s;
	        transition-duration: 0.2s;
	position: absolute;
	width: 32px;
	height: 3px;
	background-color: #d8d8d8;
	content: "";
	top: 33px; left: 0;
}
.nav-btn.open .icon:before {
	background-color: #ababab;
}	
.nav-btn .icon:after {
	-webkit-transition-duration: 0.2s;
	        transition-duration: 0.2s;
	position: absolute;
	width: 32px;
	height: 3px;
	background-color: #d8d8d8;
	content: "";
	top: 43px; left: 0;
}
.nav-btn.open .icon:after {
	background-color: #ababab;
}
.nav-btn.open .icon {
	-webkit-transition: 0.5s;
	        transition: 0.5s;
	z-index: 999;
	top: 30px;
}
.nav-btn.open .icon:before {
	-webkit-transform: rotateZ(45deg) scaleX(1.25) translate(3px, 3px);
	-ms-transform: rotateZ(45deg) scaleX(1.25) translate(3px, 3px);
	transform: rotateZ(45deg) scaleX(1.25) translate(3px, 3px);
}
.nav-btn.open .icon:after {
	-webkit-transform: rotateZ(-45deg) scaleX(1.25) translate(3px, -3px);
	-ms-transform: rotateZ(-45deg) scaleX(1.25) translate(3px, -3px);
	transform: rotateZ(-45deg) scaleX(1.25) translate(3px, -3px);
}
.nav-btn:hover {
	cursor: pointer;
}
.nav-btn:hover .icon:before, .nav-btn:hover .icon:after {
	background-color: #000;
}
.nav-btn.open:hover .icon:before, .nav-btn.open:hover .icon:after {
	background-color: #fff;
}

.menu {
	position: fixed;
	top: 0; left: 0;
	width: 100%;
	height: 100%;
	background-color: rgba(0,0,0,0.95);
	color: #fff;
	z-index: 998;
	opacity: 0;
	visibility: hidden;
	-webkit-transition: opacity 0.3s, visibility 0s 0.3s;
	        transition: opacity 0.3s, visibility 0s 0.3s;
}
.menu.open {
	opacity: 1;
	visibility: visible;
	-webkit-transition: opacity 0.3s;
	        transition: opacity 0.3s;
}
.menu nav {
	text-align: center;
	position: relative;
	top: 50%;
	-webkit-transform: translateY(-50%);
	-ms-transform: translateY(-50%);
	transform: translateY(-50%);
}
.menu nav ul li a {
	margin: 0.7em 0;
	font-size: 2.5em;
	display: inline-block;
	color: #fff;
	text-decoration: none;
}

.menu nav ul li a:hover {
	text-decoration: underline;	
}


